.cart__container {
  position: relative; }
  .cart__container .cart {
    width: 100px;
    position: absolute;
    right: 5%;
    color: #af8e68;
    padding: 10px;
    text-align: center;
    background: #fff; }

.nav {
  color: #af8e68;
  display: flex;
  width: 980px;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0; }
  .nav__logo {
    font-size: 40px;
    color: #af8e68;
