.slider {
  max-width: 1300px;
  margin: auto;
  position: relative;
  height: 600px; }
  .slider__inner {
    position: relative;
    width: 100%;
    height: 100%; }
    .slider__inner img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: 1s; }
      .slider__inner img:first-child {
        opacity: 1; }
  .slider__btns {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-40%); }
    .slider__btns .btn-right {
      position: absolute;
      right: 0; }
    .slider__btns .btn {
      background: #958f8d;
      border: none;
      height: 200px;
      width: 50px;
      opacity: 50%;
      font-size: 40px; }
      .slider__btns .btn i {
        color: #c8c4c3; }
      .slider__btns .btn:hover {
        opacity: 1; }

.indicators {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  bottom: 2%; }
  .indicators li {
    width: 20px;
    height: 20px;
    border-radius: 48% 52% 50% 50% / 45% 46% 54% 55%;
    border: 2px solid #af8e68;
    margin-left: 5px;
    color: #fff;
    text-align: center;
    font-size: 10px;
    cursor: pointer;
    padding-top: 3px; }
    .indicators li.active {
